import React, { Component } from 'react';
import axios from 'axios';

import { withRouter } from 'react-router-dom';

import Status from '../../components/Status/Status';
import Location from '../../components/Location/Location';
import Details from '../../components/Details/Details';
import Waves from '../../components/Waves/Waves';
import { baseURL, headers } from '../../api/apiStrings';
import {
  getNextTidalEvent,
  getCurrentDateTime,
  getLastTidalEvent,
  calculateWaterHeight,
  calculateTidePercentage,
  getTideDirection,
} from '../../helpers/helpers';
import DepthScale from '../../components/DepthScale/DepthScale';

class StatusPage extends Component {
  _isMounted = false;

  state = {
    loading: false,
    data: undefined,
    nextEvent: '',
    lastEvent: '',
    percentage: '',
    currentId: '',
    direction: '',
  };

  getCurrentId = () => {
    const id = this.props.match.params.id;
    return id;
  };

  componentDidMount() {
    this._isMounted = true;

    if (this.getCurrentId()) {
      this.fetchData(this.getCurrentId());
    } else if (localStorage.getItem('SR')) {
      const savedLocations = JSON.parse(localStorage.getItem('SR'))[0];
      this.props.history.push(`/${savedLocations.id}`);
    } else {
      this.props.history.push(`/0085`);
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.location !== this.props.location) {
      this.fetchData(this.getCurrentId());
    }
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  fetchData = (id) => {
    this.setState({ loading: true });
    axios
      .get(`${baseURL}${id}/TidalEvents`, headers)
      .then((res) => {
        this.setState({
          data: res.data,
          loading: false,
          currentId: this.getCurrentId(),
        });
        this.updateStateData(res.data);
      })
      .catch((err) => console.log(err));
  };

  updateStateData = (data) => {
    const currentDateTime = getCurrentDateTime();
    const nextEvent = getNextTidalEvent(this.state.data, currentDateTime);
    const lastEvent = getLastTidalEvent(this.state.data, currentDateTime);
    const percentage = calculateTidePercentage(nextEvent, currentDateTime);
    const waterHeight = calculateWaterHeight(nextEvent, lastEvent, percentage);
    const direction = getTideDirection(nextEvent, lastEvent);
    if (this._isMounted) {
      this.setState({
        percentage,
        nextEvent,
        lastEvent,
        waterHeight,
        direction,
      });
    }
  };

  render() {
    const data = this.state.data;

    return (
      <>
        <Status
          status={this.state.waterHeight}
          loading={this.state.loading}
          direction={this.state.direction}
        />
        {this.state.data && (
          <Details data={data} loading={this.state.loading} />
        )}
        <Location locations={this.props.locations} />
        <DepthScale />
        <Waves height={this.state.percentage} />
      </>
    );
  }
}

export default withRouter(StatusPage);
