import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import styles from './AddLocationForm.module.scss';
import Axios from 'axios';

import { baseURL, headers } from '../../api/apiStrings';

class AddLocationForm extends Component {
  state = {
    name: '',
    id: '',
    stations: undefined,
    error: '',
  };

  componentDidMount() {
    Axios.get(`${baseURL}`, headers).then((res) => {
      this.setState({ stations: res.data });
    });
  }

  submitHandler = (e) => {
    e.preventDefault();
    const newLocation = {
      name: this.state.name,
      id: this.state.id,
    };

    if (newLocation.name.trim().length >= 2) {
      this.props.addLocation(newLocation);
      this.props.history.push(`/${newLocation.id}`);
    }
  };

  inputChangedHandler = (e) => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  };

  cancelButtonHandler = () => {
    this.props.history.goBack();
  };

  render() {
    let stations = <p>loading stations</p>;
    let sortedStations;
    if (this.state.stations) {
      sortedStations = this.state.stations.features.sort((a, b) => {
        var nameA = a.properties.Name.toUpperCase(); // ignore upper and lowercase
        var nameB = b.properties.Name.toUpperCase(); // ignore upper and lowercase
        if (nameA < nameB) {
          return -1; //nameA comes first
        }
        if (nameA > nameB) {
          return 1; // nameB comes first
        }
        return 0; // names must be equal
      });

      stations = (
        <>
          <label>Station:</label>

          <select
            name='id'
            className={styles.AddLocationForm__select}
            onChange={this.inputChangedHandler}
          >
            <option> -- select station -- </option>
            {sortedStations
              .filter(
                (station) => station.properties.ContinuousHeightsAvailable
              )
              .map((station) => (
                <option
                  value={station.properties.Id}
                  key={station.properties.Id}
                >
                  {station.properties.Name.toUpperCase()}
                </option>
              ))}
          </select>
        </>
      );
    }

    return (
      <form className={styles.AddLocationForm} onSubmit={this.submitHandler}>
        <h3 className={styles.AddLocationForm__title}>Add Location</h3>
        <label>Location name</label>
        <p>{this.state.error}</p>
        <input
          type='text'
          name='name'
          value={this.state.name}
          onChange={this.inputChangedHandler}
          className={styles.AddLocationForm__input}
        />
        {stations}
        <button
          className={styles.AddLocationForm__button}
          disabled={this.state.name.length === '' || this.state.id === ''}
        >
          Save
        </button>
        <button
          onClick={this.cancelButtonHandler}
          className={styles.AddLocationForm__button}
        >
          Cancel
        </button>
      </form>
    );
  }
}

export default withRouter(AddLocationForm);
