import React from 'react';

import styles from './Status.module.scss';
import Spinner from '../Spinner/Spinner';
import arrow from '../../assets/arrow.svg';

const Status = ({ status, loading, direction }) => {
  let statusEls = <Spinner />;

  if (!loading) {
    let arrowStyles;

    if (direction === 'in') {
      arrowStyles = {
        transform: 'rotate(-180deg)',
      };
    } else {
      arrowStyles = {
        transform: 'none',
      };
    }

    statusEls = (
      <>
        <h1 className={styles.Status__text}>{status}</h1>
        <p className={styles.Status__units}>M</p>
        <span className={styles.Status__small}>approx</span>
        <img
          src={arrow}
          alt='arrow'
          className={styles.Status__arrow}
          style={arrowStyles}
        />
      </>
    );
  }

  return <div className={styles.Status}>{statusEls}</div>;
};

export default Status;
