//gets the next event of specified type
export const getNextEventOfType = (data, currentDateTime, eventType) => {
  const laterEvents = data.filter(
    (obj) => obj.DateTime >= currentDateTime && eventType === obj.EventType
  );
  return laterEvents[0];
};

//get the next tidal event
export const getNextTidalEvent = (data, currentDateTime) => {
  const nextEvent = data.filter(
    (obj) => new Date(obj.DateTime) > new Date(currentDateTime)
  )[0];
  return nextEvent;
};

//get the last tidal event
export const getLastTidalEvent = (data, currentDateTime) => {
  const prevEvents = data.filter(
    (obj) => new Date(obj.DateTime) <= new Date(currentDateTime)
  );
  const lastEvent = prevEvents[prevEvents.length - 1];
  return lastEvent;
};

//calculate tide height percentage
export const calculateTidePercentage = (NextEvent, currentDateTime) => {
  const timeRemaining = new Date(NextEvent.DateTime) - new Date();
  let percent = Math.round((timeRemaining / 22320000) * 100); //6hrs 12 mins

  if (NextEvent.EventType === 'HighWater') {
    percent = 100 - percent;
  }
  return percent;
};

//calculate approximate depth of water
export const calculateWaterHeight = (nextEvent, lastEvent, percentage) => {
  const difference = nextEvent.Height - lastEvent.Height;
  const onePercent = Math.abs(difference / 100);
  if (nextEvent.Height > lastEvent.Height) {
    return (nextEvent.Height - onePercent * percentage).toFixed(2);
  } else {
    return (nextEvent.Height + onePercent * percentage).toFixed(2);
  }
};

//calculate current timestamp
export const getLocalTimestamp = () => {
  const now = +new Date() / 1000;
  return now;
};

//get the direction the tide is currently moving
export const getTideDirection = (nextEvent, lastEvent) => {
  return nextEvent.Height > lastEvent.Height ? 'in' : 'out';
};

export const getCurrentDateTime = () => {
  return new Date().toISOString();
};
