import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import styles from './App.module.scss';
import Layout from './components/Layout/Layout';
import StatusPage from './pages/StatusPage/StatusPage';
import AddLocationPage from './pages/AddLocationPage/AddLocationPage';
import ManageLocationsPage from './pages/ManageLocationsPage/ManageLocationsPage';

class App extends Component {
  state = {
    locations: [],
  };

  addLocation = (location) => {
    const locations = [...this.state.locations];
    locations.push(location);
    localStorage.setItem('SR', JSON.stringify(locations));
    this.setState({ locations: locations });
  };

  removeLocation = (id) => {
    if (window.confirm('Are you sure you want to delete this location?')) {
      const locations = [...this.state.locations];
      const newLocations = locations.filter((location) => location.id !== id);
      this.saveLocally(newLocations);
      this.setState({ locations: newLocations });
    } else {
      return;
    }
  };

  //save new locations string locally
  saveLocally = (locations) => {
    localStorage.setItem('SR', JSON.stringify(locations));
  };

  componentDidMount() {
    const localLocations = localStorage.getItem('SR');
    if (localLocations) {
      this.setState({ locations: JSON.parse(localLocations) });
    }
  }

  render() {
    return (
      <BrowserRouter basename='/highwaters'>
        <div className={styles.App}>
          <Layout addMode={this.state.addLocationMode}>
            <Switch>
              <Route
                path='/add'
                exact
                render={() => (
                  <AddLocationPage addLocation={this.addLocation} />
                )}
              />
              <Route
                path='/manage'
                exact
                render={() => (
                  <ManageLocationsPage
                    locations={this.state.locations}
                    remove={this.removeLocation}
                  />
                )}
              />
              <Route
                path='/:id'
                render={() => <StatusPage locations={this.state.locations} />}
              />
              <Route
                path='/'
                render={() => <StatusPage locations={this.state.locations} />}
              />
            </Switch>
          </Layout>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
