import React from 'react';

import styles from './Details.module.scss';
import { getNextEventOfType, getCurrentDateTime } from '../../helpers/helpers';

const convertISOtoTime = (dateString) => {
  return new Date(dateString).toLocaleTimeString();
};

const Details = ({ data, loading }) => {
  let nextHighTide = getNextEventOfType(
    data,
    getCurrentDateTime(),
    'HighWater'
  );
  let nextLowTide = getNextEventOfType(data, getCurrentDateTime(), 'LowWater');

  if (!loading) {
    return (
      <aside className={styles.Details}>
        <ul className={styles.Details__ul}>
          <li>
            Next High Tide:{' '}
            <span className={styles.Details__data}>
              {convertISOtoTime(nextHighTide.DateTime)}{' '}
              {nextHighTide.Height.toFixed(2)}m
            </span>
          </li>
          <li>
            Next Low Tide:{' '}
            <span className={styles.Details__data}>
              {convertISOtoTime(nextLowTide.DateTime)}{' '}
              {nextLowTide.Height.toFixed(2)}m
            </span>
          </li>
        </ul>
      </aside>
    );
  } else {
    return null;
  }
};

export default Details;
