import React from 'react';
import { Link } from 'react-router-dom';

import Waves from '../../components/Waves/Waves';
import styles from './ManageLocationsPage.module.scss';

const ManageLocationsPage = ({ locations, remove }) => {
  return (
    <div className={styles.ManageLocationsPage}>
      <div className={styles.ManageLocationsPage__container}>
        <h3 className={styles.ManageLocationsPage__title}>manage locations</h3>
        <ul className={styles.ManageLocationsPage__list}>
          {locations.map((location) => (
            <li key={location.id} className={styles.ManageLocationsPage__li}>
              {location.name}
              <div className={styles.ManageLocationsPage__buttons}>
                <button
                  className={styles.ManageLocationsPage__button}
                  onClick={() => remove(location.id)}
                >
                  delete
                </button>
              </div>
            </li>
          ))}
        </ul>
        <Link to='/'>
          <button className={styles.ManageLocationsPage__closeButton}>
            close
          </button>
        </Link>
      </div>
      <Waves />
    </div>
  );
};

export default ManageLocationsPage;
