import React from 'react';
import { withRouter } from 'react-router-dom';

import styles from './Waves.module.scss';

import Wave1 from '../../assets/wave1.png';
import Wave2 from '../../assets/wave2.png';
import Wave3 from '../../assets/wave3.png';

const Waves = (props) => {
  let height = `10vh`;

  if (props.height) {
    height = `${props.height + 10}vh`;
  } else {
    height = '120vh';
  }

  return (
    <div className={styles.Waves}>
      <div
        className={styles.Waves__wave1}
        style={{
          backgroundImage: `url(${Wave1})`,
          height: height,
        }}
      ></div>
      <div
        className={styles.Waves__wave2}
        style={{
          backgroundImage: `url(${Wave2})`,
          height: height,
        }}
      ></div>
      <div
        className={styles.Waves__wave3}
        style={{
          backgroundImage: `url(${Wave3})`,
          height: height,
        }}
      ></div>
    </div>
  );
};

export default withRouter(Waves);
