import React from 'react';
import { withRouter, Link } from 'react-router-dom';

import styles from './Location.module.scss';

const Location = ({ locations, ...props }) => {
  const selectedHandler = (e) => {
    props.history.push(`/${e.target.value}`);
  };

  const currentID = props.match.params.id;

  const addButtonHandler = () => {
    props.history.push('/add');
  };

  const warning = (
    <div className={styles.Location}>
      <p className={styles.Location__warn}>Currently displaying demo data.</p>
      <button onClick={addButtonHandler}>Add your first location</button>
    </div>
  );

  const locationEls = (
    <div className={styles.Location}>
      <label className={styles.Location__label}>location</label>

      <select
        name='location'
        className={styles.Location__input}
        onChange={selectedHandler}
        defaultValue={currentID}
      >
        {locations.map((location) => {
          return (
            <option value={location.id} key={location.id}>
              {location.name}
            </option>
          );
        })}
        <option value='add'>add new</option>
      </select>
      <Link to='/manage'>
        <button className={styles.Location__manageBtn}>manage</button>
      </Link>
    </div>
  );

  return locations.length < 1 ? warning : locationEls;
};

export default withRouter(Location);
